body {
    background-color: #f0f0f0;
    font-family: Arial, sans-serif;
}

#vara-container {
    width: 210mm;  /* A4 Width */
    height: 297mm; /* A4 Height */
    overflow: hidden;
    border: 1px solid #ccc; /* Optional: Visualize the canvas */
    margin: 0 auto; /* Center the canvas */
    position: relative;
}
